import axios, { AxiosError, AxiosRequestConfig, AxiosResponse } from "axios";
import { getHooks } from "..";
import { ActionType } from "../reducer/util/ActionType";
import { getEndPoint } from "./util/getEndpoint";

const config: AxiosRequestConfig = {
  headers: {
    "Content-Type": "application/json",
    "Cache-Control": "no-cache",
    Accept: "application/json, text/plain",
  },
  //withCredentials: true,
  timeout: 60000,
};
const service = axios.create(config);

const requestHandler = (request: any) => {
  const { baseDispatch } = getHooks();
  baseDispatch({ type: ActionType.SET_IS_LOADING, data: { isLoading: true } });
  return request;
};
service.interceptors.request.use(requestHandler);

const errorHandler = (error: AxiosError): unknown => {
  const { baseDispatch } = getHooks();
  let msg: string = error.message;
  if (error.response && error.response.data) {
    let data: any = error.response.data;
    msg = data.message;
  }
  baseDispatch({ type: ActionType.SET_ERRORMSG, data: { errorMsg: msg } });
  baseDispatch({ type: ActionType.SET_IS_LOADING, data: { isLoading: false } });
  return Promise.reject({ ...error });
};
const successHandler = (response: AxiosResponse): AxiosResponse => {
  const { baseDispatch } = getHooks();
  if (response && response.headers && response.headers["Authorization"]) {
    service.defaults.headers.common[
      "Authorization"
    ] = `Bearer ${response.headers["token"]}`;
    baseDispatch({
      type: ActionType.SET_TOKEN,
      data: { token: response.headers["token"] },
    });
  }
  baseDispatch({
    type: ActionType.SET_ERRORMSG,
    data: { errorMsg: undefined },
  });
  baseDispatch({ type: ActionType.SET_IS_LOADING, data: { isLoading: false } });
  return response;
};
service.interceptors.response.use(successHandler, errorHandler);

const putService = (path, data, signal = undefined) => {
  return service.put(getEndPoint(path), data, {
    signal,
  });
};

const postService = (path, data, signal = undefined) => {
  return service.post(getEndPoint(path), data, {
    signal,
  });
};

const getService = (path, signal = undefined) => {
  return service.get(getEndPoint(path), {
    signal,
  });
};

export { putService, postService, getService };
